import { Button, Typography } from "@mui/material";
import React from "react";
import { FaTrash } from "react-icons/fa";
import { useDispatch } from "react-redux";
import { deleteYoutube, getUser } from "../../actions/user";
import "./YoutubeCard.css";
const YoutubeCard = ({
  url = "https://drive.google.com/drive/folders/1GYY1q7EEwXr2dSsEJ3QnV1T8IQKMHKaN",
  title = "Title Here",
  position,
  description,
  image,
  isAdmin = false,
  id,
}) => {
  const dispatch = useDispatch();

  const deleteHandler = async (id) => {
    await dispatch(deleteYoutube(id));
    dispatch(getUser());
  };

  return (
    // <div className="youtubeCard">
    //   <a href={url} target="blank">
    //     <img src={image} alt="Video" />
    //     <Typography>{title}</Typography>
    //   </a>
    <>
      <a href={url} className="projectCard" target="blank">
        <div className="democertificate">
          <img src={image} alt="Certificate"/>
        </div>
        <div>
          <Typography variant="h4"> About Work Experience</Typography>
          <Typography variant="h6">{title}</Typography>
          <Typography variant="h6">{position}</Typography>
          <Typography >{description}</Typography>
        </div>
      </a>

      {isAdmin && (
        <Button
          style={{
            margin: "auto",
            display: "block",
            color: "rgba(40,40,40,0.7)",
          }}
          onClick={() => deleteHandler(id)}
        >
          <FaTrash />
        </Button>
      )}
    </>
  );
};

export default YoutubeCard;
